.items {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 1rem;
}

.items{
  animation: formAnima 650ms forwards ;
}
.items-list {
  list-style: none;
  padding: 0;
}

.items-list__fallback {
  color: white;
  text-align: center;
}
.btnForBack button{
  font: inherit;
    cursor: pointer;
    padding: .8rem 1.6rem;
    border: .3rem solid rgba(255, 255, 255, 0.247);
    background-color: rgb(31, 31, 31);
    font-size: 1.3rem;
    border-radius: .7rem;
}
.btnForBack i{
  color: white;
  font-size: 1.4rem;

}
.btnForBack{
  position: absolute;
  left: 2%;
  top: 5%;
  animation: formAnima 650ms forwards ;

}


