.new-item__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-item__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-item__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 18rem;
    max-width: 100%;
  }
  
  .new-item__actions {
    text-align: right;
  }
  .form-cont{
      animation: formAnimation 650ms forwards ;
  }
  .new_item__date{
    width: 15rem;
  }
  .new_item__price{
    width: 11rem;
  }
  @keyframes formAnimation{
      from{
          opacity: 0;
          transform: translateY(-1.5vh);
      }
      to{
          opacity: 1;
          transform: translateY(0vh);
      }
  }