.items {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
  }
.items{
    animation: formAnima 650ms forwards ;
}
@keyframes formAnima{
    from{
        opacity: 0;
        transform: translateY(-1.5vh);
    }
    to{
        opacity: 1;
        transform: translateY(0vh);
    }
}