.App .feedback{
    position: fixed;
    right: 5%;
    bottom: 0%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    height: 40px;
    width: 8rem;
    border-top-left-radius: .7rem;
    border-top-right-radius: 0.7rem;
    background-color: rgb(31, 31, 31);  
}
.App .feedback a{
    text-decoration: none;
}
.App .feedback p{
    color: white;
}