@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Suez+One&family=Ubuntu&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.log-cont{
    background-color: #3f3f3f;
    width: 100vw;
    max-width: 100%;
    height: 100vh;
    color: #323232;
}
.log-header{
    display: flex;
    height: 150px;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.log-header p{
    display: flex;
    height: 100%;
    align-items: center;
    text-shadow: 1px 1px 1px #323232;

}
.log-header span{
    font-size: 64px;
    color: #a892ee;
    font-family: 'Suez One', serif;
}
.log-form{
    width: 450px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background-color: white;
    border-radius: 9px;
    animation: logForm 300ms;
}
@keyframes logForm{
    0%{
        height: 500px;
    }100%{
        height: 400px;
    }
}

.log-form input{
    width: 320px;
    height: 40px;
    outline: none;
    border-radius: 3px;
    padding-left: 8px;
    font-size: 15px;
    border: 1px solid rgb(209, 209, 209);
}
.log-form input:focus{
    border: black 1px solid;
}
.log-login-cont{
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.log-und-or{
    width: 50%;
    display: flex;
    justify-content:space-around;
    align-items: center;
    margin: -60px 0;
}
.log-or{
    font-size: 19px; 
}
.log-und{
    width: 100px;
    border-bottom: 1px solid black;
}

.log-login-cont button{
    background-color: #a892ee;
    border-radius: 4px;
    height: 40px;
    width: 320px;
    outline: none;
    border: none;
}
.log-form button{
    font-size: 18px;
    color: white;
}
.log-signup-btn{
    background-color: #a892ee;
    border-radius: 4px;
    height: 40px;
    width: 320px;
    outline: none;
    border: none;
    font-size: 18px;
    color: white;
}
.log-form-cont{
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.log-login-head{
    font-size: 40px;
    font-family: 'Suez One', serif;
    color: #a892ee;
    margin: -10px 0 -53px 0;
}
.log-error{
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 17px;
    color: red;
    margin: 5px 0 -5px 5px;
    height: 14px;
    /* display: none; */
}