*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.userprofile-cont{
    position: absolute;
    height: 85px;
    width: 250px;
    background-color: #a892ee;
    border-top-right-radius:55px ;
    border-bottom-right-radius:55px ;
    left: 0%;
}
.userprofile-cont-extend{
    position: absolute;
    height: 270px;
    width: 250px;
    background-color: #a892ee;
    border-top-right-radius:55px ;
    border-bottom-right-radius:55px ;
    left: 0%;
    transition: height 150ms;
}
.userprofile-innercont{
    position: relative;
}
.userprofile-innercont .clickhere{
    max-width: 110px;
    position: absolute;
    font-size: 20px;
    left: 14%;
    margin-top: 13px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
}
.clickhere-none{
    display: none;
}
.profile-img{
    max-width: 85px;
    position: absolute;
    right: 0%;
    cursor: pointer;
}
.profile-img-extend{
    max-width: 85px;
    position: absolute;
    right: 37%;
    cursor: pointer;
    animation: profileExtend 200ms;
}
@keyframes profileExtend{
    0%{
        right: 0%;
    }100%{
        right: 40%;
    }
}

.userData{
    padding-top: 90px;
    max-width: 240px;
    padding-left: 10px;
}
.userData p{
    padding: 5px 0;
    font-size: 18px;
}
.userData-none{
    display: none;
}
.userprofile-btn{
    background-color: #40005d;
    border: none;
    outline: none;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 17px;
    border-radius: 5px;
    padding: 9px 18px;
    margin:20px 0 0 10px;
    cursor: pointer;
}
.userprofile-btn-none{
    display: none;
}

.userprofile-btn-close{
    background-color: #40005d;
    border: none;
    outline: none;
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 17px;
    border-radius: 5px;
    padding: 9px 18px;
    margin:20px 0 0 10px;
    cursor: pointer;
}
.userprofile-btn-close-none{
    display: none;
}


