*{
    margin: 0px;
    padding: 0px;
}
.visual-main__cont{
    background-color: white;
    padding: 2rem;
}
.visual-cont{
    width: 80%;
    border-radius: 1.2rem;
    margin: auto;
    padding: 2rem 10rem;
    animation: borderAnim 1s;
}
.vis-charts{
    animation: chartAnim 1200ms ;
}
@keyframes chartAnim{
    from{
        opacity: 0;
        transform: translateY(-2.5vh);
    }
    to{
        opacity: 1;
        transform: translateY(0vh);
    }
}
