.item-cont {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
    margin: 1rem 0;
    background-color: #4b4b4b;
    border-radius: 0.7rem;
  }
  
  .item-cont__description {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
  }
  
  .item-cont h2 {
    color: #3a3a3a;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
  }
  .item-cont__time {
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    width: 150px;
    text-align: center;
    max-width: 150px;
    min-width: 150px;
  }
  .item-cont__price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    width: 100px;
    text-align: center;
    max-width: 100px;
    min-width: 100px;
  }
  
  
  @media (min-width: 580px) {
    .item-cont__description {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      flex: 1;
    }
  
    .item-cont__description h2 {
      font-size: 1.8rem;
    }
    .item-cont__time {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
      width: 140px;
      text-align: center;
      max-width: 140px;
      min-width: 140px;
    }
    .item-cont__price {
      font-size: 1.25rem;
      padding: 0.5rem 1.5rem;
      width: 100px;
      text-align: center;
      max-width: 100px;
      min-width: 100px;
    }
  
  }