.items-filter {
    color: white;
    padding: 0 1rem;
  }
  
  .items-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .items-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  .items-filter label:nth-child(2){
    padding-left: 30px; 
  }
  .items-filter label button{
    color: #40005d;
    border: none;
    padding: 1rem 1.4rem;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
    background-color:#a892ee ;
    border-radius: 10px;
  }
  
  .items-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }